import Vue from 'vue';
import Vuex from 'vuex';

import user from './user';
import assignment from './assignment';

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
  modules: { user, assignment },
  strict: debug,
});



